<template>
    <div class="nav__item" :class="{'nav__item--active': activeItem}" @click="open(id)">
        <h2 class="nav__item-name">{{nameItem}}</h2>
    </div>
</template>

<script>
export default {
  name: 'MenuItem',
  props: {
    id: Number,
    nameItem: String
  },
  methods: {
    open (id) {
      this.$parent.$parent.activeComponent = id
    }
  },
  computed: {
    activeItem () {
      if (this.id === this.$parent.$parent.activeComponent) {
        return true
      } else {
        return false
      }
    }
  }
}
</script>

