<template>
    <div class="photos__block-item" @mouseenter="mEnter()" @mouseleave="mLeave()">
        <div v-show="lupaActive" class="photos__block-item-lupa" @click="popup()">
            <img class="photos__block-item-lupa-img" src="../assets/images/lupa.png">
        </div>
        <img class="photos__block-item-img" :src="getImgUrl(img)" alt="img"/>
        <div class="photos__popup" v-show="activePopup" @click.self="popup()">
          <div class="photos__popup-item" @click.self="popup()">
            <img class="photos__popup-img" :src="getImgUrl(img)" />
            <img class="photos__popup-close" src="../assets/images/close.png" @click.self="popup()"/>
          </div>
        </div>
    </div>
</template>

<script>

export default {
  name: 'Photo',
  props: {
    img: String
  },
  data () {
    return {
      lupaActive: false,
      activePopup: false
    }
  },
  methods: {
    popup () {
      this.activePopup = !this.activePopup
    },
    mEnter () {
      this.lupaActive = true
    },
    mLeave () {
      this.lupaActive = false
    },
    getImgUrl (img) {
      return require('../assets/images/' + img)
    }
  }
}
</script>

<style lang="scss">
.photos__popup{
    position: fixed;
    height:100vh;
    width:100%;
    top:0;
    left:0;
    z-index: 4;
    background:rgba( 0, 43, 65, 0.6);
    display: flex;
    justify-content: center;
    align-items: center;
    &-item{
      display: flex;
      justify-content: center;
      align-items: center;
      max-width:1000px;
      width: 100%;
      max-height: 530px;
      height: 100%;
      padding: 40px 0;
      z-index: 5;
      @media (max-width: 1024px) {
        padding-right: 15px;
        padding-left: 42px;
      }
      @media (max-width: 480px) {
        padding-right: 15px;
        padding-left: 35px;
      }
    }
    &-img{
      max-width:1000px;
      width: 100%;
      max-height: 526px;
      height: 100%;
      z-index: 5;
      border-radius: 10px;
    }
    &-close{
      width: 54px;
      height: 54px;
      cursor:pointer;
      z-index: 6;
      align-self: flex-start;
      margin-top: -27px;
      margin-left: -27px;
      @media (max-width: 768px) {
        width: 40px;
        height: 40px;
        margin-top: -20px;
        margin-left: -20px;
      }
    }
}
</style>
